import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import { pathToObjects } from '@webui-one/core/keypath/kp-parser';
import { isObject, stringifyObject } from '@webui-one/core/utils/object';
import { flatten, isNonEmptyArray } from '@webui-one/core/utils/array';
import {
    isNonEmptyString,
    lstrip,
    rstrip,
} from '@webui-one/core/utils/string';
import { timeStampToISOLocale } from '@webui-one/core/utils/time';
import { color11, color1 } from '@webui-one/styles/ApplicationDefaultStyles';
import CrossSymbol from '@webui-one/styles/static/CrossSymbol';
import Loader from '../Loading/Loader';
import style from './ActionOutputStyles';

const isResultOk = (resultObj) => {
    if (!resultObj) return false;
    if (!Array.isArray(resultObj)) return false;
    if (resultObj.length !== 2) return false;

    const result = resultObj[0];
    if (!isObject(result)) return false;
    if (typeof result.name === 'undefined') return false;
    if (typeof result.value === 'undefined') return false;
    if (result.name === 'result' && result.value === 'true') return true;
    return false;
};

const createResult = (result) => {
    if (!Array.isArray(result)) {
        return (
            <ul style={style.list.body} data-handle="action-output">
                <li style={style.list.row.even}>
                    <p style={style.list.value}>{ JSON.stringify(result) }</p>
                </li>
            </ul>
        );
    }

    const rows = result.map((r, ix) => (
        <li
            style={ix % 2 === 0 ? style.list.row.even : style.list.row.odd}
            key={`action-output-result-${ix}`}
        >
            <p style={style.list.p}>{ r.name }</p>
            <p style={style.list.value}>{ r.value }</p>
        </li>
    ));
    return (
        <ul style={style.list.body} data-handle="action-output">
            { rows }
        </ul>
    );
};

function formatRelativePath(path, absolutePath) {
    const pathObjs = pathToObjects(path);
    const basePath = isNonEmptyString(absolutePath)
        ? rstrip(absolutePath, '/')
        : absolutePath;

    return pathObjs.reduce((acc, obj) => {
        if (obj.path === basePath) {
            return `./${lstrip(path.substring(obj.path.length), '/')}`;
        }
        return acc;
    }, path);
}

function formatTransValidationFailed(e, actionPath) {
    const errors = Array.isArray(e.data.errors) ? e.data.errors : [];

    const errorArray = flatten(errors.map((err) => {
        if (!isNonEmptyArray(err.paths)) return err;
        return err.paths.map(path => ({ reason: err.reason, path }));
    }));

    const total = errorArray.length;

    const items = errorArray.map(({ path, reason }, ix) => {
        const index = ix + 1;
        const formattedPath = formatRelativePath(path, actionPath);
        const message = path ? `${formattedPath} ${reason}` : reason;

        return (
            <p
                key={`validation-failed-${index}`}
                style={style.list.value}
            >
                {`${index}/${total}: ${message}`}
            </p>
        );
    });

    if (items.length === 0) {
        return null;
    }

    if (items.length === 1) {
        return items.pop();
    }

    return (
        <>
            {items}
        </>
    );
}

function formatErrorData(e, p) {
    if (e.type === 'trans.validation_failed') {
        return formatTransValidationFailed(e, p);
    }

    return (
        <p style={style.list.value}>{stringifyObject(e.data)}</p>
    );
}

const ActionOutput = ({
    error,
    path,
    result,
    resultPending,
    completedAt,
    hideSuccessfullOutput,
    onClearActionOutput,
}) => {
    const containerRef = useRef(null);
    let contents = false;

    useEffect(() => {
        if (containerRef.current) {
            containerRef.current.scrollIntoView({
                behavior: 'smooth',
                block: 'start',
                inline: 'nearest',
            });
        }
    }, [completedAt, containerRef]);

    if (error) {
        contents = (
            <ul style={style.list.body} data-handle="action-output">
                <li style={style.list.row.even} data-handle="action-error">
                    <p style={style.list.value}>Error</p>
                </li>
                {['type', 'code', 'message'].reduce((acc, errorKey) => {
                    if (errorKey in error) {
                        const comp = (
                            <li style={style.list.row.even} data-handle={`action-error-${errorKey}`} key={errorKey}>
                                <p style={style.list.value}>{`${errorKey}:`}</p>
                                <p style={style.list.value}>{error[errorKey]}</p>
                            </li>
                        );
                        acc.push(comp);
                    }
                    return acc;
                }, [])}
                {'data' in error && (
                    <li
                        style={style.list.row.errorData}
                        data-handle="action-error-data"
                    >
                        {formatErrorData(error, path)}
                    </li>
                )}
            </ul>
        );
    }

    // If there was no error in the JsonRpc call itself and
    // The result is OK and
    // You want to hide the successfull output.
    if (!contents && isResultOk(result) && hideSuccessfullOutput) {
        return null;
    }
    if (result) {
        contents = createResult(result);
    }
    if (resultPending) {
        contents = <div style={style.loader}><Loader color={color11} /></div>;
    }
    return (
        <div
            ref={containerRef}
            data-handle="action-output-container"
            style={{ position: 'relative' }}
        >
            { !resultPending && (result || error) && (
                <button
                    type="button"
                    data-handle={'clearActionOutput'}
                    style={style.clearActionOutputButton}
                    onClick={() => { onClearActionOutput(); }}
                >
                    <CrossSymbol color={color1} />
                </button>
            )}
            {contents}
            {!resultPending && completedAt && (
                <p style={style.p.info} data-handle="completed-at">
                    {`Completed at ${timeStampToISOLocale(completedAt)}`}
                </p>
            )}
        </div>
    );
};

ActionOutput.propTypes = {
    completedAt: PropTypes.number,
    error: PropTypes.shape({
        data: PropTypes.shape({}),
    }),
    path: PropTypes.string,
    result: PropTypes.oneOfType([
        PropTypes.bool,
        PropTypes.string,
        PropTypes.shape({}),
        PropTypes.arrayOf(
            PropTypes.shape({}),
        ),
    ]),
    resultPending: PropTypes.bool,
    hideSuccessfullOutput: PropTypes.bool,
    onClearActionOutput: PropTypes.func,
};

ActionOutput.defaultProps = {
    hideSuccessfullOutput: false,
};

export default ActionOutput;

export {
    isResultOk as _isResultOk,
};
