import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { selectNode } from 'modules/nodes/selectors';
import { existsThunk } from 'modules/nodes/thunks';

class EmptyCellContainer extends React.Component {
    componentDidMount() {
        const { exists, onExists, existsPending } = this.props;

        if (typeof exists === 'undefined' && !existsPending) {
            onExists();
        }
    }

    render() {
        const { exists } = this.props;
        return <div style={{ fontStyle: 'italic' }}>{exists && 'exists'}</div>;
    }
}

EmptyCellContainer.propTypes = {
    exists: PropTypes.bool,
    existsPending: PropTypes.bool,
    onExists: PropTypes.func,
};

const mapStateToProps = (state, ownProps) => ({
    exists: selectNode(state, ownProps.path).exists,
    existsPending: selectNode(state, ownProps.path).existsPending,
});

const mapDispatchToPropsEmptyLeaf = (dispatch, ownProps) => ({
    onExists() {
        const { path } = ownProps;
        dispatch(existsThunk({ path }));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToPropsEmptyLeaf,
)(EmptyCellContainer);

export {
    EmptyCellContainer as _EmptyCellContainer,
};
