import React from 'react';
import PropTypes from 'prop-types';
import { isDeepEqual } from '@webui-one/core/utils/object';

import {
    style,
    getGridCellStyle,
    getGridColumnsStyle,
} from './GridStyles';


const getRowContentWidth = (rows) => {
    if (rows && rows.children && rows.children.length > 0) {
        const widths = Array.from(rows.children).map(c => c.getBoundingClientRect().width);
        return Math.max(...widths);
    }
    return undefined;
};

const getContainerWidth = (container) => {
    if (!container) return undefined;
    return container.getBoundingClientRect().width;
};

// 16 might seems like a magic number but it should be same amount as column gap (1rem).
const COLUMN_GAP_WIDTH = 16;
const calculateGrid = (widthOfColumn, widthOfContainer, numChildren, minimumRows) => {
    let columnCount;

    if (widthOfContainer && widthOfColumn) {
        columnCount = Math.floor(widthOfContainer / (widthOfColumn + COLUMN_GAP_WIDTH)) || 1;
    }
    let rowCount = Math.ceil(numChildren / (columnCount || 1));
    rowCount = rowCount < minimumRows ?
        Math.min(minimumRows, numChildren) : rowCount;

    return { columnCount, rowCount };
};

const hasStateChanged = (state, nextState) => {
    const { columnCount, rowCount, widthOfColumn } = state;
    if (columnCount !== nextState.columnCount) return true;
    if (rowCount !== nextState.rowCount) return true;
    if (widthOfColumn !== nextState.widthOfColumn) return true;
    return false;
};

const hasPropsChanged = (props, nextProps) => {
    const { children } = props;
    if (!isDeepEqual(children, nextProps.children)) return true;
    return false;
};

const shouldRenderBindingLine = (ix, rowCount, numElements) =>
    (ix % rowCount) + 1 !== rowCount && (ix + 1) !== numElements;

const getGridChildren = children => children.reduce((acc, cur) => {
    if (cur && cur.props && cur.props.spanRow === true) return acc;
    acc.push(cur);
    return acc;
}, []);

const getSpanChildren = children => children.reduce((acc, cur, ix) => {
    if (cur && cur.props && cur.props.spanRow === true) {
        acc.push([cur, ix - 1]);
    }
    return acc;
}, []);

class Grid extends React.Component {
    constructor(props) {
        super(props);
        this.updateDimensions = this.updateDimensions.bind(this);
        this.state = {
            columnCount: undefined,
            rowCount: undefined,
            widthOfColumn: undefined,
        };
    }

    componentDidMount() {
        const { children, minimumRows } = this.props;
        if (children.length) {
            const widthOfColumn = getRowContentWidth(this.columnsDivRef);
            const widthOfContainer = getContainerWidth(this.containerDivRef);

            const { columnCount, rowCount } = calculateGrid(
                widthOfColumn,
                widthOfContainer,
                getGridChildren(children).length,
                minimumRows,
            );
            // NOTE (stelarss):
            // From react/no-did-mount-set-state documentation
            // "Updating the state after a component mount will trigger a second render()"
            // But in this case is a second render the thing we want
            //
            // eslint-disable-next-line react/no-did-mount-set-state
            this.setState({
                widthOfColumn,
                columnCount,
                rowCount,
            });
            this.subscribeResize();
        }
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (hasStateChanged(this.state, nextState)) return true;
        if (hasPropsChanged(this.props, nextProps)) return true;
        return false;
    }

    componentWillUnmount() {
        if (this.resizeObserver) this.resizeObserver.disconnect();
    }

    subscribeResize() {
        if (this.containerDivRef && typeof ResizeObserver !== 'undefined') {
            const ro = new ResizeObserver((entries) => {
                entries.forEach((entry) => {
                    const cr = entry.contentRect;
                    this.updateDimensions(cr.width);
                });
            });
            ro.observe(this.containerDivRef);
            this.resizeObserver = ro;
        }
    }

    updateDimensions(newWidthOfContainer) {
        const { widthOfColumn, columnCount: oldColumnCount } = this.state;
        const { children, minimumRows } = this.props;
        const { columnCount, rowCount } = calculateGrid(
            widthOfColumn,
            newWidthOfContainer,
            getGridChildren(children).length,
            minimumRows,
        );
        if (oldColumnCount === columnCount) {
            return;
        }
        this.setState({
            columnCount,
            rowCount,
        });
    }

    renderAsGrid(columnCount, rowCount, children) {
        const { shouldAddBinder, rowGap } = this.props;

        const spanCellIndex = children.findIndex(c => c && c.props && c.props.spanRow);

        const columnsStyle = getGridColumnsStyle(columnCount, rowCount, { rowGap, spanCellIndex });
        const items = getGridChildren(children).map((child, ix) => {
            const cellStyle = getGridCellStyle(ix, rowCount, columnCount, child);
            return (
                <div
                    style={cellStyle}
                    key={ix}
                >
                    {shouldAddBinder && shouldRenderBindingLine(ix, rowCount, children.length) && (
                        <div style={style.grid.bindingLine} />
                    )}
                    {child}
                </div>
            );
        });

        const spanItems = getSpanChildren(children).map(([child, ix]) => {
            const cellStyle = getGridCellStyle(ix, rowCount, columnCount, child);
            return (
                <div
                    style={cellStyle}
                    key={ix}
                >
                    {child}
                </div>
            );
        });

        return (
            <div
                style={columnsStyle}
                ref={(r) => { this.columnsDivRef = r; }}
            >
                { items }
                { spanItems }
            </div>
        );
    }

    render() {
        const { children, dataHandle } = this.props;
        const { columnCount, rowCount } = this.state;
        if (children.length < 1) {
            return null;
        }

        const content = this.renderAsGrid(columnCount, rowCount, children);

        return (
            <div
                ref={(c) => { this.containerDivRef = c; }}
                data-handle={dataHandle}
                style={style.grid.container}
            >
                {content}
            </div>
        );
    }
}

Grid.propTypes = {
    children: PropTypes.arrayOf(Object).isRequired,
    dataHandle: PropTypes.string,
    minimumRows: PropTypes.number,
    rowGap: PropTypes.string,
    shouldAddBinder: PropTypes.bool,
};

Grid.defaultProps = {
    minimumRows: 3,
};

export default (Grid);

export {
    calculateGrid as _calculateGrid,
    getContainerWidth as _getContainerWidth,
    getRowContentWidth as _getRowContentWidth,
    hasPropsChanged as _hasPropsChanged,
    hasStateChanged as _hasStateChanged,
    shouldRenderBindingLine as _shouldRenderBindingLine,
};
