import React from 'react';
import PropTypes from 'prop-types';
import ToolTip from '../ToolTip/ToolTip';

export default class ContentIndicator extends React.Component {
    constructor(props) {
        super(props);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
    }

    showTooltip() {
        this.tooltip.show();
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    render() {
        const {
            direction,
            toolTipText,
            toolTipStyle,
        } = this.props;

        return (
            <div
                style={toolTipStyle}
                onMouseEnter={this.showTooltip}
                onMouseLeave={this.hideTooltip}
                data-handle={'content-indicator'}
            >
                <ToolTip
                    direction={direction}
                    toolTipText={toolTipText}
                    ref={(tt) => { this.tooltip = tt; }}
                />
            </div>
        );
    }
}

ContentIndicator.propTypes = {
    direction: PropTypes.string.isRequired,
    toolTipText: PropTypes.string.isRequired,
    toolTipStyle: PropTypes.shape({}).isRequired,
};

ContentIndicator.defaultProps = {
    direction: 'up',
    toolTipText: '',
    toolTipStyle: PropTypes.shape({}),
};
