import React from 'react';
import PropTypes from 'prop-types';
import MessageBox from './MessageBox';
import style from './MessageBoxStyles';


const NoData = (props) => {
    const { onClick } = props;
    return (
        <MessageBox name="no-data-box">
            <>
                All content in this view is hidden.
                <button
                    type="button"
                    style={style.button}
                    onClick={onClick}
                    data-handle="show-everything-button"
                >
                    Show all
                </button>
            </>
        </MessageBox>
    );
};

NoData.propTypes = {
    onClick: PropTypes.func,
};

export default NoData;
