import React from 'react';
import PropTypes from 'prop-types';
import { Link } from '@webui-one/router';
import TrashcanSymbol from '@webui-one/styles/static/TrashcanSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import useSafeOnClick from '../hooks/useSafeOnClick';
import InformationButton from './Information/InformationButton';
import PresenceButton from './PresenceButton/PresenceButton';
import style from './SchemaHeaderStyles';

const SchemaHeader = ({
    exists,
    isDirty,
    isEditMode,
    isOperdata,
    isPresenceContainer,
    isRootPath,
    link,
    linkPostfix,
    name,
    onCreate,
    onDelete,
    path,
    presenceContainerOnConfigEditorRoot,
}) => {
    const safeOnClick = useSafeOnClick(onDelete);

    if (isRootPath && !presenceContainerOnConfigEditorRoot) return null;

    const shouldDisplayPresenceButton = (
        isPresenceContainer && !exists && !isOperdata
    );
    const shouldDisplayTrashcan = isPresenceContainer && isEditMode && exists;
    const shouldDisplayInfoButton = isEditMode;
    const disablePresenceButton = (
        !isPresenceContainer || exists === true
    );

    const linkName = `${name}${linkPostfix}`;

    return (
        <div style={style.rootContainer} data-handle={name}>
            <div style={isOperdata ? style.linkWrapperOper : style.linkWrapper}>
                {isDirty && (<div style={style.dot} data-handle="dot" />)}
                {shouldDisplayPresenceButton && (
                    <div style={style.button}>
                        <PresenceButton
                            key={`${path}-presence-button`}
                            path={path}
                            disabled={disablePresenceButton}
                            onCreate={onCreate}
                        />
                    </div>
                )}
                {shouldDisplayTrashcan && (
                    <button
                        type="button"
                        style={style.button}
                        onClick={safeOnClick}
                        data-handle="deleteContainer"
                    >
                        <TrashcanSymbol color={defaultStyles.color1} />
                    </button>
                )}
                {shouldDisplayInfoButton && (
                    <div
                        style={style.button}
                        data-handle="infoContainer"
                    >
                        <InformationButton
                            circleColor={defaultStyles.color1}
                            iColor={defaultStyles.color3}
                            path={path}
                        />
                    </div>
                )}
                {!presenceContainerOnConfigEditorRoot &&
                    <Link style={style.textLink} to={link}>{linkName}</Link>
                }
                {presenceContainerOnConfigEditorRoot &&
                    <span style={style.text}>{linkName}</span>
                }
            </div>
            <div style={style.line} />
        </div>
    );
};

SchemaHeader.propTypes = {
    exists: PropTypes.bool,
    isDirty: PropTypes.bool,
    isEditMode: PropTypes.bool,
    isOperdata: PropTypes.bool,
    isPresenceContainer: PropTypes.bool,
    isRootPath: PropTypes.bool,
    link: PropTypes.string.isRequired,
    linkPostfix: PropTypes.string,
    name: PropTypes.string.isRequired,
    onCreate: PropTypes.func,
    onDelete: PropTypes.func,
    path: PropTypes.string.isRequired,
    presenceContainerOnConfigEditorRoot: PropTypes.bool,
};

SchemaHeader.defaultProps = {
    dirty: false,
    dirtyRefCount: 0,
    exists: false,
    isPresenceContainer: false,
    linkPostfix: '/',
};

export default SchemaHeader;
