import React, {
    useCallback,
    useEffect,
    useState,
} from 'react';
import PropTypes from 'prop-types';
import Loader from '@webui-one/components/Loading/Loader';
import { delay } from '@webui-one/core/utils/async';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import style from './StateButtonStyles';

const states = {
    DONE: 'done',
    ERROR: 'error',
    IDLE: 'idle',
    PENDING: 'pending',
};

const COOLDOWN_TIME = 2000;

const useAsyncState = (
    func,
    cooldownTime,
    {
        funcs: {
            AbortCtrl = AbortController,
        } = {},
    } = {},
) => {
    const [state, setState] = useState(states.IDLE);
    const [running, setRunning] = useState(false);
    const execute = useCallback(() => { setRunning(true); }, []);

    useEffect(() => {
        if (!running) return () => undefined;
        const controller = new AbortCtrl();
        const signal = controller.signal;
        const updateState = newState => setState(newState);
        const updateRunning = newState => setRunning(newState);

        const stop = () => {
            updateState(states.IDLE);
            updateRunning(false);
        };

        async function run() {
            try {
                updateState(states.PENDING);
                await func({ signal });
                updateState(states.DONE);
                await delay(cooldownTime)();
            } catch (error) {
                updateState(states.ERROR);
                await delay(cooldownTime)();
            }
            stop();
        }

        run();

        return () => {
            controller.abort();
            stop();
        };
    }, [AbortCtrl, cooldownTime, func, running, setRunning, setState]);

    return [state, execute];
};


function StateButton({
    dataHandle,
    cooldownTime = COOLDOWN_TIME,
    doneLabel = 'done',
    enabled,
    errorLabel = 'error',
    label,
    onClick,
    styles: {
        disabledStyle = defaultStyles.buttonDisabled,
        enabledStyle = defaultStyles.button,
        loaderColor = defaultStyles.color3,
        loaderStyle = style.loader,
        wrapperStyle = style.wrapper,
    } = {},
    funcs: {
        useFunc = useAsyncState,
    } = {},
} = {}) {
    const [state, execute] = useFunc(onClick, cooldownTime);
    const disabled = !enabled || state !== states.IDLE;

    return (
        <div style={wrapperStyle}>
            <button
                data-handle={dataHandle}
                disabled={disabled}
                onClick={execute}
                style={disabled ? disabledStyle : enabledStyle}
                type="button"
            >
                <div style={style.label}>
                    {(state === states.IDLE) && (
                        <div>{label}</div>
                    )}
                    {(state === states.ERROR) && (
                        <div>{errorLabel}</div>
                    )}
                    {(state === states.DONE) && (
                        <div>{doneLabel}</div>
                    )}
                    {(state === states.PENDING) && (
                        <div style={loaderStyle}>
                            <Loader color={loaderColor} />
                        </div>
                    )}
                </div>
            </button>
        </div>
    );
}

StateButton.propTypes = {
    cooldownTime: PropTypes.number,
    dataHandle: PropTypes.string,
    doneLabel: PropTypes.string,
    enabled: PropTypes.bool,
    errorLabel: PropTypes.string,
    funcs: PropTypes.shape({
        useFunc: PropTypes.func,
    }),
    label: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
    styles: PropTypes.shape({
        enabledStyle: PropTypes.shape({}),
        disabledStyle: PropTypes.shape({}),
        loaderStyle: PropTypes.shape({}),
        wrapperStyle: PropTypes.shape({}),
    }),
};

export default StateButton;

export {
    states,
    COOLDOWN_TIME,
    useAsyncState,
};
