import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { loadStatus as caseLoadStatus } from 'modules/nodes/reducer';
import { selectNode } from 'modules/nodes/selectors';

import Case from './Case';

const CaseContainer = ({ path, isLoading, SchemaContainer }) => (
    <Case path={path} isLoading={isLoading} SchemaContainer={SchemaContainer} />
);

CaseContainer.propTypes = {
    path: PropTypes.string,
    isLoading: PropTypes.bool,
    SchemaContainer: PropTypes.elementType.isRequired,
};

const mapStateToProps = (state, ownProps) => {
    const { path } = ownProps;
    const { loadStatus } = selectNode(state, path);
    const isLoading = loadStatus === caseLoadStatus.LOADING;
    return {
        path,
        isLoading,
    };
};

export default connect(
    mapStateToProps,
)(CaseContainer);
