import React from 'react';
import PropTypes from 'prop-types';

import CaseContainer from 'Common/Components/ConfigRender/CaseContainer';
import style from './ChoiceStyles';

const getMarkerStyle = (isSetCase, isVisibleCase) => {
    if (!isSetCase) return style.noMarker;
    if (isVisibleCase) return style.selectedMarker;
    return style.marker;
};

class Choice extends React.Component {
    constructor(props) {
        super(props);
        this.onSelectCase = this.onSelectCase.bind(this);
        this.state = {};
    }

    onSelectCase(newSelectedCaseValue) {
        const { selectedCaseValue } = this.state;
        if (selectedCaseValue === newSelectedCaseValue) return;
        this.setState({ selectedCaseValue: newSelectedCaseValue });
    }

    render() {
        const { caseValue, cases, name, path, SchemaContainer } = this.props;
        const { selectedCaseValue } = this.state;
        const visibleCase = selectedCaseValue || caseValue || cases[0];

        const caseTabs = cases.map(c => (
            <div
                key={c}
                style={c === visibleCase ? style.activeTab : style.tab}
                data-handle={`case-${c}`}
                data-case-selected={c === visibleCase}
                data-case-active={c === caseValue}
            >
                <button
                    type="button"
                    data-handle={`case-button-${c}`}
                    style={style.tabButton}
                    onClick={() => this.onSelectCase(c)}
                >
                    {c}
                    <div style={getMarkerStyle(c === caseValue, c === visibleCase)} />
                </button>
            </div>
        ));

        const visibleCasePath = `${path}/${visibleCase}`;

        return (
            <div style={style.main} data-handle={`choice-${name}`}>
                <div style={style.title}>{name}</div>
                <div style={style.tabContainer}>
                    {caseTabs}
                </div>
                <div style={style.caseWrapper}>
                    <CaseContainer
                        key={visibleCasePath}
                        path={visibleCasePath}
                        SchemaContainer={SchemaContainer}
                    />
                </div>
            </div>
        );
    }
}

Choice.propTypes = {
    cases: PropTypes.arrayOf(PropTypes.string),
    caseValue: PropTypes.string,
    name: PropTypes.string.isRequired,
    path: PropTypes.string.isRequired,
    SchemaContainer: PropTypes.elementType.isRequired,
};

export default Choice;
