import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Messages, translate } from 'modules/intl';
import { loadModules } from 'modules/devices/actions';

import {
    selectCompletedAt,
} from 'modules/yangActions/selectors';

import {
    tryGetDeviceName,
} from '@webui-one/core/utils/path';

import ActionContainer from './ActionContainer';
import MessageBox from '@webui-one/components/ConfigRender/MessageBox';

class NoCapabilitiesContainer extends React.Component {
    componentDidUpdate(nextProps) {
        const { connectActionCompletedAt, onLoadModules, path } = this.props;
        if (connectActionCompletedAt !== nextProps.connectActionCompletedAt) {
            onLoadModules(path);
        }
    }
    render() {
        const { deviceKey, path } = this.props;
        return (
            <>
                <MessageBox name={path}>
                    {translate(Messages.COMMON.NO_CAPABILITIES_FOUND_FOR_DEVICE)}
                </MessageBox>
                <ActionContainer
                    key={`/ncs:devices/device{${deviceKey}}/connect`}
                    label={'connect'}
                    path={`/ncs:devices/device{${deviceKey}}/connect`}
                    hideSuccessfullOutput
                    refreshPath={path}
                />
            </>
        );
    }
}

NoCapabilitiesContainer.propTypes = {
    path: PropTypes.string,
    deviceKey: PropTypes.string,
    onLoadModules: PropTypes.func.isRequired,
    connectActionCompletedAt: PropTypes.number,
};

const mapStateToProps = (state, { path }) => {
    const deviceKey = tryGetDeviceName(path);
    return ({
        deviceKey,
        connectActionCompletedAt:
            selectCompletedAt(state, `/ncs:devices/device{${deviceKey}}/connect`),
    });
};

const mapDispatchToProps = dispatch => ({
    onLoadModules(path) {
        return dispatch(loadModules(path));
    },
});

export default connect(mapStateToProps, mapDispatchToProps)(NoCapabilitiesContainer);

export {
    NoCapabilitiesContainer as _NoCapabilitiesContainer,
    mapStateToProps as _mapStateToProps,
    mapDispatchToProps as _mapDispatchToProps,
};
