import React from 'react';
import PropTypes from 'prop-types';
import InformationButton from './Information/InformationButton';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import ActionSymbol from '@webui-one/styles/static/ActionSymbol';
import { overlayStyle, actionStyle as style } from '@webui-one/styles/LeafStyles';
import Link from '../Link';

const ActionLink = ({
    isEditMode,
    isOperdata,
    label,
    LinkComponent,
    path,
    to,
}) => (
    <div
        data-handle={label}
        style={style.actionContainer}
    >
        <div style={style.wrapperWrapper}>
            <LinkComponent
                to={to}
                style={isOperdata ? style.actionWrapperOperdata : style.actionWrapper}
            >
                <div style={style.symbol}>
                    <ActionSymbol color={defaultStyles.color3} />
                </div>
                <div style={style.label}>{label}</div>
            </LinkComponent>
            {isEditMode && (
                <div style={overlayStyle.editModeOverlay} data-handle="overlay">
                    <div style={overlayStyle.symbol}>
                        {<InformationButton
                            circleColor={defaultStyles.color3}
                            iColor={defaultStyles.color1}
                            path={path}
                         />
                        }
                    </div>
                </div>
            )}
        </div>
    </div>
);

ActionLink.propTypes = {
    label: PropTypes.string,
    LinkComponent: PropTypes.elementType,
    to: PropTypes.string,
    path: PropTypes.string,
    isEditMode: PropTypes.bool,
    isOperdata: PropTypes.bool,
};

ActionLink.defaultProps = {
    LinkComponent: Link,
};

export default ActionLink;
