import { connect } from 'react-redux';
import { Link } from '@webui-one/router';
import BackToSourceLink from '@webui-one/components/ConfigRender/BackToSourceLink/BackToSourceLink';
import { selectActionPath } from 'modules/transactions/selectors';
import { selectServicePoints } from 'modules/services/selectors';
import { getServicePointsAndSchemas } from 'modules/services/actions';

const mapStateToProps = (state, ownProps) => {
    const actionPath = selectActionPath(state, ownProps.path);
    const servicePoints = selectServicePoints(state);

    return {
        actionPath,
        LinkComponent: Link,
        servicePoints,
    };
};

const mapDispatchToProps = dispatch => ({
    onLoad() {
        dispatch(getServicePointsAndSchemas());
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(BackToSourceLink);

export {
    BackToSourceLink as _BackToSourceLinkContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
