import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { selectKeepInputParams as _selectKeepInputParams } from 'modules/yangActions/selectors';
import {
    loadActionAction as _loadActionAction,
    resetActionThunk as _resetActionThunk,
    runActionThunk as _runActionThunk,
    keepInputParamsAction as _keepInputParamsAction,
} from 'modules/yangActions/actions';
import StateButton from '@webui-one/components/ConfigRender/StateButton';
import ActionRunButton from '@webui-one/components/ConfigRender/ActionRunButton';
import ActionOutputContainer from './ActionOutputContainer';
import CheckBox from '@webui-one/components/ConfigRender/CheckBox/CheckBox';
import useSafeOnClick from '@webui-one/components/hooks/useSafeOnClick';
import style from './ActionContainerStyles';

function ActionContainer({
    enabled,
    hideSuccessfullOutput,
    keepInputParams,
    label,
    onKeepInputParams,
    onLoadAction,
    onResetAction,
    onRunAction,
    hasChildren,
    path,
    refreshPath,
} = {}) {
    useEffect(() => {
        onLoadAction(path);
    }, [onLoadAction, path]);

    const onClick = () => {
        const shouldRefresh = path === refreshPath && !keepInputParams;
        return onRunAction(path, shouldRefresh, keepInputParams);
    };

    const safeOnClick = useSafeOnClick(onClick);
    const onResetClick = useSafeOnClick(() => onResetAction(path));

    return (
        <>
            <div style={style.wrapper}>
                <ActionRunButton
                    enabled={enabled}
                    onClick={safeOnClick}
                    label={label}
                />
                { hasChildren && (
                    <>
                        <StateButton
                            onClick={onResetClick}
                            dataHandle="reset-action-parameters"
                            enabled
                            label="Reset action parameters"
                            styles={{ wrapperStyle: style.resetContainerStyles }}
                        />
                        <div style={style.checkboxWrapper}>
                            <CheckBox
                                editable
                                value={keepInputParams}
                                onSetValue={() => onKeepInputParams(path)}
                                name="keepInputParams"
                            >
                                <div style={style.checkboxLabel}>keep input parameters</div>
                            </CheckBox>
                        </div>
                    </>
                )}
            </div>
            <ActionOutputContainer
                path={path}
                hideSuccessfullOutput={hideSuccessfullOutput}
            />
        </>
    );
}

ActionContainer.propTypes = {
    enabled: PropTypes.bool,
    label: PropTypes.string,
    path: PropTypes.string,
    keepInputParams: PropTypes.bool,
    onLoadAction: PropTypes.func,
    onResetAction: PropTypes.func,
    onRunAction: PropTypes.func,
    onKeepInputParams: PropTypes.func,
    hasChildren: PropTypes.bool,
    hideSuccessfullOutput: PropTypes.bool,
    refreshPath: PropTypes.string,
};

ActionContainer.defaultProps = {
    hideSuccessfullOutput: false,
    enabled: true,
    label: 'action',
    keepInputParams: false,
};

const mapStateToProps = (state, ownProps) => {
    const {
        path,
        funcs: {
            selectKeepInputParams = _selectKeepInputParams,
        } = {},
    } = ownProps;
    return ({
        keepInputParams: selectKeepInputParams(state, path),
    });
};

const mapDispatchToProps = (
    dispatch,
    {
        funcs: {
            loadActionAction = _loadActionAction,
            resetActionThunk = _resetActionThunk,
            runActionThunk = _runActionThunk,
            keepInputParamsAction = _keepInputParamsAction,
        } = {},
    } = {},
) => ({
    onLoadAction(actionPath) {
        dispatch(loadActionAction({ actionPath }));
    },
    onResetAction(actionPath) {
        return dispatch(resetActionThunk({ actionPath }));
    },
    onRunAction(actionPath, shouldRefresh, keepInputParams) {
        return dispatch(runActionThunk({ actionPath, shouldRefresh, keepInputParams }));
    },
    onKeepInputParams(actionPath) {
        dispatch(keepInputParamsAction(actionPath));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ActionContainer);

export {
    ActionContainer as _ActionContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
