import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { range, range1 } from '@webui-one/core/utils/array';

const style = {
    grid: {
        container: {
            overflow: 'visible',
            margin: '1rem 0',
        },
        columns: {
            overflow: 'visible',
            display: 'grid',
            gridAutoFlow: 'column',
        },
        columnItem: {
            WebkitColumnBreakInside: 'avoid',
            pageBreakInside: 'avoid',
            breakInside: 'avoid',
            display: 'flex',
            backgroundColor: 'inherit',
            position: 'relative',
        },
        bindingLine: {
            backgroundColor: defaultStyles.backgroundColor1,
            bottom: '-17px',
            height: '1.1rem',
            left: '50%',
            position: 'absolute',
            width: '0.125rem',
        },
    },
};
const getGridColumnGapStyle = (columnIndex, rowCount) => {
    if (!columnIndex) return {};
    if (!rowCount) return {};

    return {
        gridArea: `1 / ${columnIndex} / ${rowCount + 1} / ${columnIndex}`,
    };
};

const shouldAppendSpanRow = (spanCellIndex, rowIndex, rowCount, columnCount) => {
    if (spanCellIndex < 0) return false;
    const rowCells = range(0, columnCount).map(c => rowIndex + rowCount * c);

    if (rowCells.includes(spanCellIndex - 1)) {
        return true;
    }
    return false;
};

const getGridColumnsStyle = (
    columnCount,
    rowCount,
    {
        rowGap = 'normal',
        spanCellIndex = -1,
    } = {},
) => {
    if (!(columnCount && rowCount)) {
        return { display: 'flex' };
    }

    const gridTemplateAreas = range1(1, rowCount).reduce((acc, cur, ix) => {
        const cells = range(0, columnCount).map(c => `c${ix + rowCount * c}`);
        acc.push(`'${cells.join(' ')}'`);
        if (shouldAppendSpanRow(spanCellIndex, ix, rowCount, columnCount)) {
            const row = range(0, columnCount).map(() => `span-row-${ix}`);
            acc.push(`'${row.join(' ')}'`);
        }
        return acc;
    }, []).join(' ');

    return {
        ...style.grid.columns,
        rowGap,
        columnGap: '1rem',
        gridTemplateAreas,
        gridTemplateColumns: [...Array(columnCount)].map(() => '1fr').join(' '),
    };
};

const getRowIndex = (ix, rowCount) => ix % rowCount;

const getGridCellStyle = (ix, rowCount, columnCount, child) => {
    let gridArea = `c${ix}`;

    if (child && child.props && child.props.spanRow === true) {
        gridArea = range(0, columnCount).map(
            () => `span-row-${getRowIndex(ix, rowCount)}`,
        ).join(' / ');
    }

    return {
        ...style.grid.columnItem,
        gridArea,
    };
};

export {
    style,
    getGridCellStyle,
    getGridColumnsStyle,
    getGridColumnGapStyle,
    getRowIndex as _getRowIndex,
};
