import { connect } from 'react-redux';
import {
    selectError,
    selectResult,
    selectResultPending,
    selectCompletedAt,
} from 'modules/yangActions/selectors';
import { clearActionOutputAction } from 'modules/yangActions/actions';
import ActionOutput from '@webui-one/components/ConfigRender/ActionOutput';

const mapStateToProps = (state, ownProps) => ({
    error: selectError(state, ownProps.path),
    path: ownProps.path,
    result: selectResult(state, ownProps.path),
    resultPending: selectResultPending(state, ownProps.path),
    completedAt: selectCompletedAt(state, ownProps.path),
});

const mapDispatchToProps = (dispatch, ownProps) => ({
    onClearActionOutput() {
        dispatch(clearActionOutputAction(ownProps.path));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ActionOutput);
