import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { loadStatus as caseLoadStatus } from 'modules/nodes/reducer';
import { selectNode } from 'modules/nodes/selectors';
import { isDeepEqual, isEmpty } from '@webui-one/core/utils/object';

import {
    clearCaseAction,
} from 'modules/nodes/actions';

import {
    getCaseThunk,
} from 'modules/nodes/thunks';

import {
    reloadCaseDataThunk,
} from 'modules/data/actions';

import Choice from './Choice';

class ChoiceContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount() {
        const {
            choicePath,
            loadStatus,
            nodepath,
            path,
            onGetCase,
        } = this.props;

        if (loadStatus !== caseLoadStatus.LOADING) {
            onGetCase(path, nodepath, choicePath);
        }
    }

    shouldComponentUpdate(nextProps) {
        const {
            cases,
            caseValue,
            loadStatus,
        } = this.props;

        if (loadStatus !== nextProps.loadStatus) return true;
        if (caseValue !== nextProps.caseValue) return true;
        if (!isDeepEqual(cases, nextProps.cases)) return true;

        return false;
    }

    componentDidUpdate(prevProps) {
        const {
            caseValue,
            choicePath,
            loadStatus,
            nodepath,
            path,
            onGetCase,
            onReloadCaseData,
        } = this.props;

        if (loadStatus === undefined || path !== prevProps.path) {
            onGetCase(path, nodepath, choicePath);
        }
        if (loadStatus !== 'LOADING' && caseValue !== undefined && caseValue !== prevProps.caseValue) {
            onReloadCaseData(caseValue);
        }
    }

    componentWillUnmount() {
        const { onClearCase, path } = this.props;
        onClearCase(path);
    }

    render() {
        const {
            caseValue,
            cases,
            name,
            path,
            SchemaContainer,
        } = this.props;

        if (cases && cases.length === 0) return null;

        return (
            <Choice
                cases={cases}
                caseValue={caseValue}
                name={name}
                path={path}
                SchemaContainer={SchemaContainer}
            />
        );
    }
}

ChoiceContainer.propTypes = {
    cases: PropTypes.arrayOf(PropTypes.string),
    caseValue: PropTypes.string,
    choicePath: PropTypes.string.isRequired,
    loadStatus: PropTypes.string,
    name: PropTypes.string.isRequired,
    nodepath: PropTypes.string.isRequired,
    onClearCase: PropTypes.func,
    onGetCase: PropTypes.func,
    onReloadCaseData: PropTypes.func,
    path: PropTypes.string.isRequired,
    SchemaContainer: PropTypes.elementType.isRequired,
};

const selectCaseNode = (state, ownProps) => {
    const { path, nodepath } = ownProps;
    const node = selectNode(state, nodepath);
    if (!isEmpty(node)) return node;
    return selectNode(state, path);
};

const mapStateToProps = (state, ownProps) => {
    const node = selectCaseNode(state, ownProps);

    const { caseValue, loadStatus } = node;

    return {
        caseValue,
        loadStatus,
    };
};

const mapDispatchToProps = (dispatch, ownProps) => ({
    onClearCase(path) {
        dispatch(clearCaseAction(path));
    },
    onGetCase(path, nodepath, choice) {
        dispatch(getCaseThunk({ path, nodepath, choice }));
    },
    onReloadCaseData(caseValue) {
        const {
            showOperdata,
            path,
            parentNodepath,
        } = ownProps;
        dispatch(reloadCaseDataThunk({
            path,
            parentNodepath,
            caseValue,
            showOperdata,
        }));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ChoiceContainer);

export {
    ChoiceContainer as _ChoiceContainer,
    mapStateToProps as _mapStateToProps,
    mapDispatchToProps as _mapDispatchToProps,
    selectCaseNode as _selectCaseNode,
};
