import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { pathToObjects } from '@webui-one/core/keypath/kp-parser';
import { isFunction } from '@webui-one/core/utils/functional';
import { last } from '@webui-one/core/utils/array';
import { isObject } from '@webui-one/core/utils/object';
import Link from '../../Link';
import styles from './BackToSourceLinkStyles';

const getServicePointNames = servicePoints =>
    Object
        .values(servicePoints)
        .map(sp => last(pathToObjects(sp.path)));

const tryFindDeviceData = (pathObjects) => {
    const item = pathObjects[2];
    if (item && item.parentPath === '/ncs:devices/device') {
        return item;
    }
    return null;
};

const tryFindServiceData = (pathObjects, servicesPoints) => {
    const servicePathObjects = servicesPoints
        ? getServicePointNames(servicesPoints)
        : [];
    let item;

    pathObjects.forEach(pathObject => servicePathObjects.forEach((s) => {
        if (s.path === pathObject.parentPath) {
            item = pathObject;
        }
    }));

    return item;
};
function BackToSourceLink({
    actionPath,
    LinkComponent,
    onLoad,
    path,
    servicePoints,
}) {
    useEffect(() => {
        if (!isObject(servicePoints) && isFunction(onLoad)) {
            onLoad();
        }
    }, [actionPath, onLoad, path, servicePoints]);
    if (!path) return null;
    const pathObjects = pathToObjects(path);

    if (actionPath && actionPath !== path) {
        return (
            <LinkComponent
                key="3"
                data-handle="action-path"
                style={styles.backToSourceLink}
                to={`/webui-one/ConfigurationEditor${actionPath}`}
            >
                {`Navigate to action container '${actionPath}'`}
            </LinkComponent>
        );
    }

    const deviceData = tryFindDeviceData(pathObjects);
    if (deviceData) {
        return (
            <LinkComponent
                key="1"
                data-handle="device-manager-source-link"
                style={styles.backToSourceLink}
                to="/webui-one/DeviceManager"
            >
                {`See '${deviceData.name}' in Device manager`}
            </LinkComponent>
        );
    }

    const serviceData = tryFindServiceData(pathObjects, servicePoints);
    if (serviceData) {
        return (
            <LinkComponent
                key="2"
                data-handle="service-manager-source-link"
                style={styles.backToSourceLink}
                to={`/webui-one/ServiceManager${serviceData.parentPath}`}
            >
                {`See '${serviceData.name}' in Service manager`}
            </LinkComponent>
        );
    }

    return null;
}

BackToSourceLink.propTypes = {
    path: PropTypes.string.isRequired,
    servicePoints: PropTypes.shape(Object),
    actionPath: PropTypes.string,
    LinkComponent: PropTypes.elementType,
    onLoad: PropTypes.func,
};

BackToSourceLink.defaultProps = {
    LinkComponent: Link,
};

export {
    tryFindDeviceData as _tryFindDeviceData,
    tryFindServiceData as _tryFindServiceData,
};

export default BackToSourceLink;
