import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { _JsonRpcError as JsonRpcError } from '@webui-one/core/jsonrpc';
import { isChildOfPath } from '@webui-one/core/utils/path';
import { isFunction } from '@webui-one/core/utils/functional';
import Loader from '@webui-one/components/Loading/Loader';
import { selectRawLoading as selectLoading } from 'modules/nodes/selectors';
import { getDataThunk } from 'modules/data/actions';
import { selectError } from 'modules/data/selectors';
import ipcPropTypes from 'modules/ipc/propTypes';
import ErrorBox from '@webui-one/components/ConfigRender/ErrorBox';
import SchemaContainer from 'Common/Components/ConfigRender/SchemaContainer';

const currentPathModified = (path, modifiedPath) => (
    path === modifiedPath || isChildOfPath(path, modifiedPath)
);

class DataContainer extends React.Component {
    componentDidMount() {
        this.load();
    }

    shouldComponentUpdate(nextProps) {
        const {
            error,
            includeSubfolders,
            ipcModification,
            path,
            showActions,
            showEmpty,
            showOperdata,
        } = this.props;

        if (nextProps.ipcModification
            && ipcModification !== nextProps.ipcModification
            && currentPathModified(path, nextProps.ipcModification.path)
        ) {
            return true;
        }

        if (error !== nextProps.error) {
            return true;
        }

        if (path !== nextProps.path) {
            return true;
        }
        if (includeSubfolders !== nextProps.includeSubfolders) {
            return true;
        }
        if (showOperdata !== nextProps.showOperdata) {
            return true;
        }
        if (showEmpty !== nextProps.showEmpty) {
            return true;
        }
        if (showActions !== nextProps.showActions) {
            return true;
        }

        return false;
    }

    componentDidUpdate(prevProps) {
        const {
            includeSubfolders,
            ipcModification,
            path,
            showEmpty,
            showOperdata } = this.props;
        const {
            includeSubfolders: prevSubfolders,
            ipcModification: prevIpcModification,
            path: prevPath,
            showEmpty: prevShowEmpty,
            showOperdata: prevShowOperdata,
        } = prevProps;

        if (prevPath !== path ||
            prevSubfolders !== includeSubfolders ||
            prevShowEmpty !== showEmpty ||
            prevIpcModification !== ipcModification ||
            prevShowOperdata !== showOperdata) {
            this.load();
        }
    }

    componentWillUnmount() {
        if (this.aborter && isFunction(this.aborter.abort)) {
            this.aborter.abort();
        }
    }

    load() {
        const { onLoad, path, includeSubfolders, showEmpty, showOperdata } = this.props;
        if (this.aborter && isFunction(this.aborter.abort)) {
            this.aborter.abort();
        }
        this.aborter = onLoad(path, includeSubfolders, showEmpty, showOperdata);
    }

    render() {
        const { error, isLoading } = this.props;
        if (isLoading === true) {
            return <Loader color="white" />;
        }
        if (error) {
            return <ErrorBox error={error} />;
        }
        return (
            <SchemaContainer {...this.props} />
        );
    }
}

DataContainer.defaultProps = {
    hideIndicators: false,
    showBackToSourceLink: false,
    isLoading: false,
    path: '',
};

DataContainer.propTypes = {
    error: PropTypes.oneOfType([
        PropTypes.instanceOf(JsonRpcError),
        PropTypes.objectOf(Object),
    ]),
    hideIndicators: PropTypes.bool,
    includeSubfolders: PropTypes.bool,
    ipcModification: ipcPropTypes.ipcModification,
    isLoading: PropTypes.bool,
    onLoad: PropTypes.func.isRequired,
    path: PropTypes.string.isRequired,
    showActions: PropTypes.bool,
    showBackToSourceLink: PropTypes.bool,
    showEmpty: PropTypes.bool,
    showOperdata: PropTypes.bool,
};

const mapStateToProps = (state, { path }) => ({
    error: selectError(state, path),
    isLoading: selectLoading(state, path),
});

const mapDispatchToProps = dispatch => ({
    onLoad(path, includeSubfolders, showEmpty, showOperdata) {
        return dispatch(getDataThunk({
            path,
            includeSubfolders,
            showEmpty,
            showOperdata,
        }));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(DataContainer);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    DataContainer as _DataContainer,
};
