import { connect } from 'react-redux';
import { Link } from '@webui-one/router';
import {
    selectLocationPathnameFromPath,
} from 'modules/routing/selectors';
import { selectIsEditMode } from 'ConfigurationEditor/reducer';
import {
    selectName,
} from 'modules/schemas/reducer';

import ActionLink from '@webui-one/components/ConfigRender/ActionLink';

const mapStateToProps = (state, { path }) => ({
    isEditMode: selectIsEditMode(state),
    label: selectName(state, path),
    LinkComponent: Link,
    path,
    to: selectLocationPathnameFromPath(state, path),
});

export default connect(
    mapStateToProps,
)(ActionLink);
