import PropTypes from 'prop-types';
import React from 'react';

import Loader from '@webui-one/components/Loading/Loader';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import style from './CaseStyles';

import { CONFIGURATION_EDITOR_NAME } from 'ConfigurationEditor/defines';

// TODO: ENG-18495 fix hardcoded props to SchemaContainer
// eslint-disable-next-line
class Case extends React.Component {
    render() {
        const { isLoading, path, SchemaContainer } = this.props;

        return (
            <div style={style.wrapper}>
                <SchemaContainer
                    key={path}
                    path={path}
                    applicationName={CONFIGURATION_EDITOR_NAME}
                    includeSubfolders
                    isEditMode
                    isCase
                    showActions
                    showEmpty
                    showOperdata
                />
                {isLoading && (
                <div style={style.loader}>
                    <Loader color={defaultStyles.color6} />
                </div>
)}
            </div>
        );
    }
}

Case.propTypes = {
    path: PropTypes.string.isRequired,
    isLoading: PropTypes.bool,
    SchemaContainer: PropTypes.elementType.isRequired,
};

export default Case;
