import { selectDirty } from 'modules/nodes/selectors';

import {
    selectConfigExists,
    selectExists,
} from 'modules/data/selectors';
import { escapeKey } from '@webui-one/core/utils/path';
import { lstrip } from '@webui-one/core/utils/string';
import {
    createThunk,
    deleteThunk,
} from 'modules/nodes/thunks';
import {
    selectIsRootPath,
    selectLocationPathnameFromApplicationName,
    selectPathFromApplicationName,
} from 'modules/routing/selectors';

import { selectIsEditMode } from 'ConfigurationEditor/reducer';
import { connect } from 'react-redux';
import { pathToObjects } from '@webui-one/core/keypath/kp-parser';
import SchemaHeader from '@webui-one/components/ConfigRender/SchemaHeader';

const isConfigEditorRootPath = (path) => {
    const pathObjects = pathToObjects(path);
    return pathObjects.length === 1;
};

const generateName = (rootPath, path) => (
    lstrip(path.split(`${rootPath}/`).pop(), '/')
);

const mapStateToProps = (state, ownProps) => {
    const { isConfig, isPresenceContainer, path } = ownProps;

    const rootPath = selectPathFromApplicationName(state, ownProps.applicationName);
    const name = generateName(rootPath, path);
    const isEditMode = selectIsEditMode(state);
    const presenceContainerOnConfigEditorRoot = isPresenceContainer && isConfigEditorRootPath(path);
    const exists = presenceContainerOnConfigEditorRoot
        ? selectConfigExists(state, path)
        : selectExists(state, path);

    return {
        exists,
        isConfig,
        isDirty: selectDirty(state, path),
        isEditMode,
        isPresenceContainer,
        isRootPath: selectIsRootPath(state, path),
        link: selectLocationPathnameFromApplicationName(
            ownProps.applicationName,
            escapeKey(ownProps.path),
        ),
        name,
        path,
        presenceContainerOnConfigEditorRoot,
    };
};

const mapDispatchToProps = (dispatch, { dependants, caseAncestors, path }) => ({
    onCreate() {
        return dispatch(createThunk({ caseAncestors, dependants, path }));
    },
    onDelete() {
        return dispatch(deleteThunk({ caseAncestors, dependants, path, deleteChildren: true }));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(SchemaHeader);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    generateName as _generateName,
};
