SchemaContainer.defaultProps = {
    showBackToSourceLink: false,
};

const isSchemaLoading = (schema, state, path) => {
    if (!!schema && (schema.loadStatus === loadStatus.LOADING)) {
        return true;
    }
    const configIsLoading = selectConfigIsLoading(state, path);
    const moduleIsLoading = selectModuleIsLoading(state, path);

    return configIsLoading === true || moduleIsLoading === true;
};

const isActionWidgetKind = schema => schema &&
    getWidgetKind(schema) === widgetKinds.KIND_ACTION;

