import React from 'react';
import PropTypes from 'prop-types';
import AddSymbol from '@webui-one/styles/static/AddSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import useSafeOnClick from '../../hooks/useSafeOnClick';
import style from './PresenceButtonStyles';

const PresenceButton = ({ onCreate, disabled, path }) => (
    <button
        type="button"
        style={style.button}
        disabled={disabled}
        onClick={useSafeOnClick(onCreate)}
        data-handle={`PresenceButton-${path}`}
    >
        {disabled && (
            <AddSymbol
                symbolColor={defaultStyles.color6}
                borderColor={defaultStyles.color6}
                bgColor={defaultStyles.color3}
            />
        )}
        {!disabled && <AddSymbol />}
    </button>
);

PresenceButton.propTypes = {
    disabled: PropTypes.bool,
    onCreate: PropTypes.func,
    path: PropTypes.string,
};

PresenceButton.defaultProps = {
    disabled: false,
};

export default PresenceButton;
