import PropTypes from 'prop-types';
import React from 'react';
import ActionSymbol from '@webui-one/styles/static/ActionSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { actionStyle as style } from '@webui-one/styles/LeafStyles';

const ActionRunButton = ({ enabled, label, onClick }) =>
    (
    /* eslint-disable jsx-a11y/no-static-element-interactions */
        <div style={style.wrapperWrapper}>
            <div
                data-handle={'ActionRunButton'}
                disabled={!enabled}
                style={style.actionWrapper}
                onClick={onClick}
            >
                <div style={style.symbol}>
                    <ActionSymbol
                        color={defaultStyles.color3}
                    />
                </div>
                <div style={style.label}>{`Run ${label} action`}</div>
            </div>
        </div>
    );


ActionRunButton.propTypes = {
    enabled: PropTypes.bool,
    label: PropTypes.string,
    onClick: PropTypes.func,
};

export default ActionRunButton;
